import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BannerManageComponent } from './bannerManage/bannerManage.component';
import { LuanchManageComponent } from './luanchManage/luanchManage.component';
import { PopupManageComponent } from './popupManage/popupManage.component';

const routes: Routes = [
  {
    path: 'bannerManage/index',
    data: {
      title: '轮播图管理'
    },
    component: BannerManageComponent
  },

  {
    path: 'popupManage/index',
    data: {
      title: '首页广告管理'
    },
    component: PopupManageComponent
  },

  {
    path: 'luanchManage/index',
    data: {
      title: '启动管理'
    },
    component: LuanchManageComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OperateRoutingModules {}
