import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
// 注册当前路由
import { OperateRoutingModules } from './Operate-routing.module';
// 导入子页面组件

import { BannerManageComponent } from './bannerManage/bannerManage.component';
import { LuanchManageComponent } from './luanchManage/luanchManage.component';
import { PopupManageComponent } from './popupManage/popupManage.component';

// 引入面包屑
import { SharedModule } from 'src/app/components/shared.module';

@NgModule({
  declarations: [
    BannerManageComponent,
    PopupManageComponent,
    LuanchManageComponent
  ],
  imports: [CommonModule, SharedModule],
  exports: [OperateRoutingModules],
  providers: []
})
export class OperateModule {}
